@import '@modules/constants/index.scss';

$middle-color: #C0C0C0;

@keyframes clippath-piece {

    0%,
    100% {
        clip-path: inset(0 0 95% 0);
    }

    25% {
        clip-path: inset(0 95% 0 0);
    }

    50% {
        clip-path: inset(95% 0 0 0);
    }

    75% {
        clip-path: inset(0 0 0 95%);
    }
}

.piece-animator {
    visibility: hidden;
}

.piece-container {
    width           : auto;
    height          : 0;
    padding-bottom  : 100%;
    background-color: inherit !important;
    visibility      : visible;
    position        : relative;
}

.piece-container.active {

    &::before,
    &::after {
        content      : '';
        position     : absolute;
        top          : -4%;
        left         : -4%;
        right        : -4%;
        bottom       : -4%;
        border       : 6px solid $active-color;
        transition   : all .5s;
        border-radius: 12px;
        animation    : clippath-piece 3s infinite linear;
    }

    &::after {
        animation: clippath-piece 3s infinite -1.5s linear;
    }
}

img.piece {
    width : 90%;
    height: 90%;
    margin: 5%;
}

img.target-box {
    position: absolute;
    top     : -2%;
    left    : -2%;
    width   : 104%;
    height  : 104%;
}

:export {
    middle: $middle-color;
    black : $black-color;
    white : $white-color;
}