@import '@modules/constants/index.scss';

@keyframes clippath-core {

    0%,
    100% {
        clip-path: inset(0 0 95% 0);
    }

    25% {
        clip-path: inset(0 95% 0 0);
    }

    50% {
        clip-path: inset(95% 0 0 0);
    }

    75% {
        clip-path: inset(0 0 0 95%);
    }
}

.rune-container {
    width         : auto;
    height        : 0;
    padding-bottom: 100%;

    .core-container {
        padding: 15%;
        cursor : default;

        img.core-img {
            height: 100%;
            width : 100%;
        }

        .core-img-container {
            position: relative;
            cursor  : pointer;
        }

        .core-img-container.active {

            &::before,
            &::after {
                content   : '';
                position  : absolute;
                top       : -2%;
                left      : -2%;
                right     : -2%;
                bottom    : -2%;
                border    : 3px solid $active-color;
                transition: all .5s;
                animation : clippath-core 3s infinite linear;
            }

            &::after {
                animation: clippath-core 3s infinite -1.5s linear;
            }
        }
    }
}

:export {
    black: $black-color;
    white: $white-color;
}