$black-color: #000000;
$white-color: #FFFFFF;
$active-color: #808080;

.black-item {
    background-color: $black-color !important;
}

.white-item {
    background-color: $white-color !important;
}

.black-text {
    color: $black-color !important;
}

.white-text {
    color: $white-color !important;
}

.left-align {
    display        : flex;
    justify-content: flex-start;
    align-items    : center;
    flex-direction : row;
}

.right-align {
    display        : flex;
    justify-content: flex-end;
    align-items    : center;
    flex-direction : row;
}

:export {
    black: $black-color;
    whire: $white-color;
}