@import '@modules/constants/index.scss';

$heartbeat-size    : 24px;
$middle-line-size  : 56px;
$heartbeat-small-r : 8px;
$heartbeat-large-r : 12px;

.middle-line {
    font-size: 0;
}

:export {
    black         : $black-color;
    white         : $white-color;
    heartbeatSize : $heartbeat-size;
    middleLineSize: $middle-line-size;
    heartbeatSR   : $heartbeat-small-r;
    heartbeatLR   : $heartbeat-large-r;
}